.pictures {
  display: flex;

  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  .picture {
    width: 13vw;
    height: 35%;
    margin: 0 6px;
    margin-bottom: 14px;
    .imageContainer {
      position: relative;
      overflow: hidden;
      width: 13vw;
      height: 13vw;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: all 0.3s ease;
        &:hover {
          transform: scale(1.25);
        }
      }
    }
    .product-title {
      font-weight: 700;
      color: #39393e;
      margin-top: 6px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
    }
    .product-description {
      margin-top: 6px;
      color: #66666a;
      font-weight: 400;
      margin-bottom: 10px;
    }
    .product-price {
      color: #003354;
      font-weight: 500;
    }
  }
}
@media screen and (max-width: 555px) {
  .recommend-product {
    height: 700px;
    h2 {
      display: none;
    }
    .morePicture {
      display: none;
    }
    .pictures {
      display: none;
    }
  }
}
