* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Helvetica Neue, Helvetica, Arial, PingFang TC, Heiti TC,
    Microsoft JhengHei, sans-serif;
  font-size: 0.935rem;
  letter-spacing: 0.05em;
  color: #39393e;
}
a {
  text-decoration: none;
  color: black;
}
ul {
  list-style-type: none;
}
:root {
  --text-color: #39393e;
  --nav-size: 48px;
  --border: 1px solid #d3d3d5;
  --border-radius: 1px;
  --bg: white;
}

.box-title {
  font-weight: 700;
  font-size: 1.34rem;
  border-bottom: solid 1px #d3d3d5;

  padding-bottom: 14px;
  margin-bottom: 24px;
}
.box-title-r {
  font-weight: 600;
  font-size: 1.34rem;

  border-top: solid 1px #d3d3d5;
  padding-bottom: 14px;
  padding-top: 24px;
}
.morePicture {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4rem;
  button {
    margin-top: 14px;
    padding: 9px 14px;
    min-width: 240px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 400;
    color: #39393e;
    border: #d3d3d5 1px solid;
    background-color: white;
    &:hover {
      background-color: #f7f7f8;
    }
  }
}
