.product-main {
  .item-title {
    font-size: 1.625rem;
    word-spacing: 0.025em;
    letter-spacing: 0.65px;
    line-height: 1.4;
    font-weight: 700;

    color: #39393e;
  }
  .item-review {
    margin: 14px 0;
    i {
      color: #f16c5d;
    }
    i:nth-child(5) {
      margin-right: 10px;
    }
    span {
      margin-left: 10px;
      color: #2e90b7;
    }
  }
  .item-price {
    color: #10567b;
    font-size: 1.875rem;
  }
  .tag {
    display: flex;
    width: 33%;
    margin: 14px 0;
    border: #2e90b7 1px solid;

    dt {
      background: #2e90b7;
      color: #fff;
      padding: 4px 8px;
      font-weight: 500;
    }
    dd {
      color: #2e90b7;
      padding: 4px 8px;
      margin-left: 0;
      font-weight: 500;
    }
  }
  .event {
    color: #2e90b7;
    &:hover {
      text-decoration: underline;
    }

    i {
      margin-right: 6px;
      color: #f16c5d;
    }
  }
  .margin-b {
    height: 36px;
  }
  select {
    width: 25%;
    height: 42px;
    border: 1px solid #d3d3d5;
    display: block;
    margin-bottom: 14px;
  }
  .add-btn {
    cursor: pointer;
    border-radius: 4px;
    text-align: center;
    width: 100%;
    color: #fff;
    background: #f16c5d;
    border: none;
    padding: 9px 14px;
    margin-bottom: 10px;
    &:hover {
      background-color: #e66658;
    }
    i {
      margin-right: 6px;
      color: #fff;
    }
  }
  .collect-btn {
    cursor: pointer;
    border-radius: 4px;
    text-align: center;
    margin-bottom: 10px;
    width: 100%;
    padding: 9px 14px;
    color: #39393e;
    border: #d3d3d5 1px solid;
    &:hover {
      background-color: #f7f7f8;
    }
    i {
      margin-right: 6px;
      color: #929295;
    }
  }
  ul {
    li {
      color: #66666a;
      margin-bottom: 6px;
    }
  }
}
